<template>
  <div class="step1">
    <div class="step1__content">
      <div class="step1__loading">
        <div class="step1__loading-title"> 1</div>
        <div class="step1__loading-show">
          <div class="step1__loading-show-filled"></div>
        </div>
      </div>
      <div class="step1__body">
        <div class="step1__input">
          <p class="step1__input-name">
            
            <span class="step1__input-name-require">*</span>
          </p>
          <input :class="'step1__input-value '+(this.errorFIO?'step1__error-input':'')" type="text" autocomplete="new_fio" name="fio" v-model="fio" v-on:keyup="onKeyUpInput($event, 'fio')">
          <div class="step1__error" v-if="errorFIO"><span>{{ errorFIO }}</span></div>
        </div>
        <div class="step1__input">
          <p class="step1__input-name">
            
            <span class="step1__input-name-require">*</span>
          </p>
          <input :class="'step1__input-iin step1__input-value '+(this.errorIIN?'step1__error-input':'')" type="text" autocomplete="new_iin" name="iin" v-model="iin" v-on:keyup="onKeyUpInput($event, 'iin')">
          <div class="step1__error" v-if="errorIIN"><span>{{ errorIIN }}</span></div>
        </div>
        <div class="step1__input">
          <p class="step1__input-name">
             
            <span class="step1__input-name-require">*</span>
          </p>
          <input :class="'step1__input-phone step1__input-value '+(this.errorPhone?'step1__error-input':'')" type="text" autocomplete="new_phone" name="phone" v-model="phoneNumber" v-on:keydown="onKeyDownPhoneNumber($event)" v-on:keyup="onKeyUpPhoneNumber()">
          <div class="step1__error" v-if="errorPhone"><span>{{ errorPhone }}</span></div>
        </div>
        <div class="step1__input">
          <p class="step1__input-name">
             
            <span class="step1__input-name-require">*</span>
          </p>
          <input :class="'step1__input-email step1__input-value '+(this.errorEmail?'step1__error-input':'')" type="text" autocomplete="new_email" name="email" v-model="email" v-on:keyup="onKeyUpInput($event, 'email')">
          <div class="step1__error" v-if="errorEmail"><span>{{ errorEmail }}</span></div>
        </div>
        <div class="step1__input">
          <p class="step1__input-name">
             
            <span class="step1__input-name-require">*</span>
          </p>
          <div class="step1__input-passwords">
            <label :class="'step1__passwordInside '+(this.errorPassword?'step1__error-input-password-password':'')"
                   for="password"
            >
              <input :class="'step1__input-password step1__input-value-password-password'"
                     :type="passwordType"
                     id="password"
                     name="password"
                     autocomplete="new_password"
                     v-model="password"
                     v-on:keyup="onKeyUpInput($event, 'password')"
              >
              <img class="step1__passwordIcon" :src="passwordIcon" v-on:click="onClickPasswordIcon">
            </label>
<!--            <div :class="'step1__input-password-icon '+(errorPassword?'step1__error-input-password-icon':'')">-->
<!--              <img :src="passwordIcon" v-on:click="onClickPasswordIcon">-->
<!--            </div>-->
          </div>
          <div class="step1__error" v-if="errorPassword"><span>{{ errorPassword }}</span></div>
        </div>
      </div>
      <div class="step1__footer">
        <div class="step1__error" v-if="error"><span>{{ error }}</span></div>
        <div class="step1__continue">
          <input class="step1__submit" type="button" value="  >" v-on:click="submitRequestFirstStep()">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const axios = require('axios');

export default {
  data() {
    return {
      fio: '',
      iin: '',
      phoneNumber: '',
      phoneNumberOriginal: '',
      operatorsPhoneNumber: [
        { number: '700' },
        { number: '701' },
        { number: '702' },
        { number: '705' },
        { number: '706' },
        { number: '707' },
        { number: '708' },
        { number: '709' },
        { number: '727' },
        { number: '747' },
        { number: '750' },
        { number: '751' },
        { number: '760' },
        { number: '761' },
        { number: '762' },
        { number: '763' },
        { number: '764' },
        { number: '771' },
        { number: '775' },
        { number: '776' },
        { number: '777' },
        { number: '778' },
      ],
      email: '',
      password: '',
      token: '',
      error: '',
      errorFIO: '',
      errorIIN: '',
      errorPhone: '',
      errorEmail: '',
      errorPassword: '',
      passwordType: 'password',
      passwordIcon: '/images/show-password.png',
      goesLoading: '',
      mobileVersion: '',
    }
  },
  mounted() {
    if (localStorage.getItem('logged')) {
      this.$router.push({path: '/notifications'});
    }
    localStorage.setItem('token', '');
    this.$store.commit('SET_LOGGED','');
    this.$store.commit('SET_FOOTER',false);
    this.step1MobileVersion();
  },
  methods: {
    /* MOBILE VERSION */
    step1MobileVersion() {
      if (window.innerWidth < 1160) {
        this.phoneNumber = '7';
        this.mobileVersion = true;
      } else {
        this.phoneNumber = '+7 (';
        this.mobileVersion = false;
      }
    },
    /*  */
    onClickPasswordIcon() {
      if (this.passwordIcon === '/images/show-password.png') {
        this.passwordType = 'text';
        this.passwordIcon = '/images/show-hide-password.png';
      }
      else {
        this.passwordType = 'password';
        this.passwordIcon = '/images/show-password.png';
      }
    },
    onKeyUpInput(e, name) {
      if (e.key === 'Enter') {
        if (name === 'fio') {
          document.getElementsByClassName('step1__input-iin')[0].focus();
        }
        else if (name === 'iin') {
          document.getElementsByClassName('step1__input-phone')[0].focus();
        }
        else if (name === 'phone') {
          document.getElementsByClassName('step1__input-email')[0].focus();
        }
        else if (name === 'email') {
          document.getElementsByClassName('step1__input-password')[0].focus();
        }
        else if (name === 'password') {
          this.submitRequestFirstStep();
        }
      }
      if (name === 'fio') {
        if (e.key.toUpperCase() >= '' && e.key.toUpperCase() <= '' || e.key.toUpperCase() === '') {
          if (this.fio.length === 1 || this.fio[this.fio.length-2] === ' ') {
            this.fio = (this.fio.split('').splice(0,this.fio.length-1).join('')+e.key.toUpperCase());
          }
        }
        if (!this.fio) {
          this.errorFIO = '    ';
        } else if (!this.validateFIO(this.fio)) {
          this.errorFIO = '    ';
        } else if (!this.validateFullFIO(this.fio)) {
          this.errorFIO = '   ( :  )';
        } else {
          this.errorFIO = '';
        }
      } else if (name === 'iin') {
        if (!this.iin) {
          this.errorIIN = '    ';
        } else if (!this.validateIIN(this.iin)) {
          this.errorIIN = ' ';
        } else {
          this.errorIIN = '';
        }
      } else if (name === 'phone' && !this.mobileVersion) {
        if (!this.phoneNumberOriginal) {
          this.errorPhone = '     ';
        } else if (!this.validatePhoneNumber(this.phoneNumberOriginal)) {
          this.errorPhone = '    ';
        } else if (!(this.phoneNumberOriginal.length === 10)) {
          this.errorPhone = '  ';
        } else {
          this.errorPhone = '';
        }
      } else if (name === 'email') {
        if (!this.email) {
          this.errorEmail = '     ';
        } else if (!this.validateEmail(this.email)) {
          this.errorEmail = ' email';
        } else {
          this.errorEmail = '';
        }
      } else if (name === 'password') {
        if (!this.password) {
          this.errorPassword = '    ';
        } else if (!(this.password.length > 5)) {
          this.errorPassword = '       6 ';
        } else if (this.checkPassword(this.password)) {
          this.errorPassword = '    ';
        } else {
          this.errorPassword = '';
        }
      }
    },
    checkPassword(password) {
      for (let i = 0; i < password.length; i++) {
        if (password[i].toUpperCase() >= '' && password[i].toUpperCase() <= '' || password[i].toUpperCase() === '') {
          return true;
        }
      }
      return false;
    },
    checkPhoneNumber(phoneNumber2) {
      if (phoneNumber2[0] === '7') return true;
      else {
        this.error = '  .  ';
        return false;
      }
    },
    validatePhoneNumber2Error(text) {
      this.error = text;
      return false;
    },
    validatePhoneNumber2(phoneNumber2) {
      if (!phoneNumber2) this.validatePhoneNumber2Error('   .    ');
      else if (phoneNumber2.length === 11) {
        if (phoneNumber2[0] === '8') {
          if (this.checkPhoneNumber(phoneNumber2.substr(1))) return ('7'+phoneNumber2.substr(1));
          else return false;
        }
        else if (phoneNumber2[0] === '7') {
          if (this.checkPhoneNumber(phoneNumber2.substr(1))) return ('7'+phoneNumber2.substr(1));
          else return false;
        }
        else this.validatePhoneNumber2Error('  .  ');
      }
      else if (phoneNumber2.length === 12) {
        if (phoneNumber2[0] === '+' && phoneNumber2[1] === '7') {
          if (this.checkPhoneNumber(phoneNumber2.substr(2))) return phoneNumber2.substr(1);
          else return false;
        }
        else return this.validatePhoneNumber2Error('  .  ');
      }
      else if (phoneNumber2.length === 10) {
        this.checkPhoneNumber(phoneNumber2);
        return false;
      }
      else return this.validatePhoneNumber2Error('  .  ');
    },
    async submitRequestFirstStep() {
      if (this.goesLoading) return;
      this.goesLoading = true;
      this.error = '';
      if (this.validateStep1()) return;
      this.$store.commit('SET_LOADING', true);
      let phoneNumberRequest = '';
      if (this.mobileVersion) {
        this.phoneNumberOriginal = this.validatePhoneNumber2(this.phoneNumber);
        if (this.phoneNumberOriginal) phoneNumberRequest = this.phoneNumberOriginal;
        else return;
      }
      else phoneNumberRequest = '7'+this.phoneNumberOriginal;
      await axios.post('https://crediter.kz/api/firstStep', {
        'fio': this.fio,
        'iin': this.iin,
        'phone': phoneNumberRequest,
        'email': this.email,
        'password': this.password,
      })
          .then(async response => {
            if (response.data.success) {
              this.$store.commit('SET_LOADING', false);
              await this.$session.set('fio', this.fio);
              await this.$session.set('iin', this.iin);
              await this.$session.set('email', this.email);
              await this.$session.set('password', this.password);
              await this.$session.set('phoneNumber', phoneNumberRequest);
              this.$router.push({path: '/confirm'});
              this.goesLoading = false;
            }
            else {
              this.$store.commit('SET_LOADING', false);
              this.error = response.data.message;
              this.goesLoading = false;
            }
          })
          .catch(error => {
            this.$store.commit('SET_LOADING', false);
            this.error = error;
            this.goesLoading = false;
          });
    },
    validateStep1() {
      this.onKeyUpInput({key:'Enter'},'fio');
      this.onKeyUpInput({key:'Enter'},'iin');
      this.onKeyUpInput({key:'Enter'},'email');
      this.onKeyUpInput({key:'Enter'},'phone');
      this.onKeyUpInput({key:'Enter'},'password');
      if (!this.error && !this.errorFIO && !this.errorIIN && !this.errorEmail && !this.errorPhone && !this.errorPassword) {
        return false;
      }
      else return true;
    },
    validateFIO(fio) {
      fio = fio.split(' ').join('').split('').map((e) => {if(e.toUpperCase() >= '' && e.toUpperCase() <= '' || e.toUpperCase() === '')return true;else return false});
      for (let i = 0; i < fio.length; i++) {
        if (!fio[i]) return false;
      }
      return true;
    },
    validateFullFIO(fio) {
      fio = fio.split(' ').filter((elm) => { if(elm !== '') return elm; });
      let fioValidate = false;
      if (fio.length < 2) return false;
      for (let i = 0; i < fio.length; i++) {
        if (!(fio[i][0] >= '' && fio[i][0] <= '' || fio[i][0] === '') && (fio[i][0] >= '' && fio[i][0] <= '' || fio[i][0] === '')) fioValidate = true;
      }
      if (fioValidate) return false;
      else return true;
    },
    validateIIN(iin) {
      return (12 === iin.length && typeof parseInt(iin) === 'number' && this.isChecksumValid(iin));
    },
    isChecksumValid(iin) {
      let weights = [];
      for (let i=1;i<12;i++) weights.push(i);
      let weights2 = [];
      for (let i=3;i<12;i++) weights2.push(i);
      weights2 = [...weights2, 1, 2];

      let checksum = this.calc(iin, weights);

      if (checksum === 10) {
        checksum = this.calc(iin, weights2);
      }

      return checksum < 10 ? parseInt(iin.substr(11, 1)) === checksum : false;
    },
    calc(iin, weights) {
      let convolution = 0;
      for (let i = 0; i < 11; i++) {
        convolution += iin[i] * weights[i];
      }
      return convolution % 11;
    },
    validateEmail(email) {
      let emailValid = email.split('').map((elm) => {
        if (elm.toUpperCase() >= 'A' &&
            elm.toUpperCase() <= 'Z' ||
            elm >= '0' && elm <= '9' ||
            elm === '@' || elm === '.' ||
            elm === '-' || elm === '_' ||
            elm === '#' || elm === '$' ||
            elm === '%' || elm === '\'' ||
            elm === '&' || elm === '*' ||
            elm === '+' || elm === '/' ||
            elm === '^' || elm === '=' ||
            elm === '?' || elm === '`' ||
            elm === '{' || elm === '}' ||
            elm === '~' || elm === '|'
        ) return true;
        else return false;
      });
      let emailVal = true;
      for(let i=0;i<emailValid.length;i++) if(!emailValid[i]) emailVal=false;
      if (email.indexOf('@') > 0 &&
          email.split('').reverse().join('').indexOf('.') > 0 &&
          email.split('').reverse().join('').indexOf('@') > email.split('').reverse().join('').indexOf('.')-1 &&
          emailVal
      ) return true;
      else return false;
    },
    validatePhoneNumber(n) {
      let operators = this.operatorsPhoneNumber;
      let corresponds = false;
      for (let i = 0; i < operators.length; i++) {
        if (n[0] === operators[i].number[0] && n[1] === operators[i].number[1] && n[2] === operators[i].number[2]) {
          corresponds = true;
        }
      }
      if (corresponds) return true;
      else return false;
    },
    onKeyDownPhoneNumber(e) {
      if (!this.mobileVersion) {
        if (e.key === 'Enter') {
          document.getElementsByClassName('step1__input-email')[0].focus();
          if (!this.phoneNumberOriginal) {
            this.errorPhone = '     ';
          } else if (!this.validatePhoneNumber(this.phoneNumberOriginal)) {
            this.errorPhone = '    ';
          } else if (!(this.phoneNumberOriginal.length === 10)) {
            this.errorPhone = '  ';
          } else {
            this.errorPhone = '';
          }
          return;
        }
        if (this.phoneNumberOriginal.length > 9 && e.key !== 'Backspace') {
          if (!this.phoneNumberOriginal) {
            this.errorPhone = '     ';
          } else if (!this.validatePhoneNumber(this.phoneNumberOriginal)) {
            this.errorPhone = '    ';
          } else if (!(this.phoneNumberOriginal.length === 10)) {
            this.errorPhone = '  ';
          } else {
            this.errorPhone = '';
          }
          e.preventDefault();
          return;
        }
        if (e.key >= '0' && e.key <= '9') {
          this.phoneNumberOriginal += e.key;
        }
        else if (e.key === 'Backspace') {
          this.phoneNumberOriginal = this.phoneNumberOriginal.slice(0,-1);
        }
        e.preventDefault();
        let n = this.phoneNumberOriginal.toString();
        let pn = '+7 (';
        for (let i = 0; i < n.length; i++) {
          if (i < 3) pn += n[i];
          else if (i > 2 && i < 6) {
            if (i === 3) pn += ') ';
            pn += n[i];
          }
          else if (i > 5 && i < 8) {
            if (i === 6) pn += ' ';
            pn += n[i];
          }
          else if (i > 7) {
            if (i === 8) pn += '-';
            pn += n[i];
          }
          else break;
        }
        this.phoneNumber = pn;
        if (!this.phoneNumberOriginal) {
          this.errorPhone = '     ';
        } else if (!this.validatePhoneNumber(this.phoneNumberOriginal)) {
          this.errorPhone = '    ';
        } else if (!(this.phoneNumberOriginal.length === 10)) {
          this.errorPhone = '  ';
        } else {
          this.errorPhone = '';
        }
      }
    },
    onKeyUpPhoneNumber() {
      if (this.mobileVersion) {
        this.phoneNumberOriginal = this.phoneNumber;
        return true;
      }
    },
    checkCyrillic(text) {
      for (let i = 0; i < text.length; i++) if (text[i] >= '' && text[i] <= '' || text[i] === '') return false;
      return true;
    },
    checkLatin(text) {
      for (let i = 0; i < text.length; i++) if (text[i] >= 'A' && text[i] <= 'Z') return false;
      return true;
    },
  },
}
</script>

<style>
@import url('/css/block/step1.css');
@import url('/css/block/mobile/step1.css');
</style>
